import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import CreateButton from "../../../Ui/Button/CreateButton";
import FormInput from "../../../Ui/Input/FormInput";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import style from "../../FinanceManager/Income/Income.module.css";
import Modal from "../../../Ui/Modal/Modal";
import FormFile from "../../../Ui/Input/FormFile";
import FormTextarea from "../../../Ui/Input/FormTextarea";
import { useFileUpload } from "../../../Hooks/ApiHooks/Auth/auth-api";
import QvipleLoader from "../../../Loader/QvipleLoader";
import { useAdmissionAddDocument } from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
const AddRequiredDocument = ({
  onClose,
  aid,
  onRefetch,
  isEdit,
  documentMenu,
  onEdit,
}) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    document_name: "",
    document_key: "",
    applicable_to: "",
  });
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [file, setFile] = useState("");

  const [fileUpload] = useFileUpload();
  const [admissionAddDocument] = useAdmissionAddDocument();
  useEffect(() => {
    if (isEdit) {
      setState({
        document_name: documentMenu?.document_name ?? "",
        document_key: documentMenu?.document_key ?? "",
        applicable_to: documentMenu?.applicable_to ?? "",
      });
      setFile({
        originalName: "document preview",
      });
    }
  }, [isEdit, documentMenu]);
  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };
  const onFileUpload = (e) => {
    if (e.target.files[0]) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setFile({
            name: e.target.files[0]?.name,
            key: res.data?.imageKey,
            type: e.target.files[0]?.type,
          });
          setState((prev) => ({
            ...prev,
            document_key: res.data?.imageKey,
          }));

          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  const onAddDocument = () => {
    let validation = requiredField(state, ["document_key", "applicable_to"]);
    if (existingRequiredField(validation)) {
      if (isEdit) {
        onEdit(state);
      } else {
        setDisabled((pre) => !pre);
        admissionAddDocument({
          aid: aid,
          document: state,
        })
          .then(() => {
            onRefetch();
            setDisabled((pre) => !pre);
            onClose();
          })
          .catch({});
      }
    } else {
      setErrorField(validation);
    }
  };
  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6> {t(isEdit ? "update_document" : "add_new_document")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <FormInput
              label={t("document_name")}
              placeholder={t("document_nape_placeholder")}
              name={"document_name"}
              value={state.document_name}
              type={"text"}
              onChange={onInputChange}
              errorShow={errorField["document_name"]}
            />
            <FormTextarea
              label={t("document_applicable_to")}
              placeholder={t("document_applicable_to_placeholder")}
              name={"applicable_to"}
              value={state.applicable_to}
              type={"text"}
              onChange={onInputChange}
            />
            <FormFile
              label={t("sample_documnt")}
              name={"document"}
              value={file}
              onChange={onFileUpload}
            />
            <CreateButton
              label={isEdit ? "update" : "create"}
              onAction={onAddDocument}
            />
          </UserMemberContentWrapper>
        </div>
      </Modal>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default AddRequiredDocument;
