import { useTranslation } from "react-i18next";
import style from "../AdmissionManager.module.css";
import {
  assestsUserAdmissionUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useState } from "react";
import FormFileView from "../../../Ui/Input/FormFileView";
import RequiredDocumentCardMenu from "./RequiredDocumentCardMenu";

const RequiredDocumentCard = ({ document, onRefetch, aid }) => {
  const { t } = useTranslation();
  const [documentMenu, setDocumentMenu] = useState("");

  const onClose = () => {
    setDocumentMenu("");
  };
  const onMenu = () => {
    setDocumentMenu(document);
  };
  return (
    <>
      <div
        className={style.document_card}
        style={{
          position: "relative",
        }}
      >
        <img
          src={`${assestsUserAdmissionUrl}/required-document.svg`}
          alt="document icon"
        />
        <div className={style.document_card_text}>
          <p>
            {document?.document_name ?? ""}
            {document?.applicable_to ? (
              <span
                style={{
                  fontSize: "11px",
                }}
              >
                {` (${t("applicable_to")}- ${document?.applicable_to})`}
              </span>
            ) : (
              ""
            )}
          </p>
          <FormFileView
            value={
              document?.document_key
                ? {
                    key: document?.document_key,
                  }
                : ""
            }
            shownLabel="view_attachment"
            customFormInputContainer={{
              marginBlock: "0.3rem",
            }}
          />
        </div>
        <img
          src={`${assestsUserFinanceUrl}/menu.svg`}
          alt="menu icon"
          onClick={onMenu}
          className={style.menu_icon}
          title="Menu"
        />
      </div>
      {documentMenu && (
        <RequiredDocumentCardMenu
          onClose={onClose}
          onRefetch={onRefetch}
          aid={aid}
          documentMenu={documentMenu}
        />
      )}
    </>
  );
};

export default RequiredDocumentCard;
