import { useState } from "react";
import ModalMenu from "../../../Ui/Modal/ModalMenu";
import { useTranslation } from "react-i18next";
import {
  useAdmissionRemoveDocument,
  useAdmissionUpdateDocument,
} from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import AddRequiredDocument from "./AddRequiredDocument";

const RequiredDocumentCardMenu = ({
  aid,
  onClose,
  documentMenu,
  onRefetch,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [edit, setEdit] = useState(false);
  const [admissionRemoveDocument] = useAdmissionRemoveDocument();
  const [admissionUpdateDocument] = useAdmissionUpdateDocument();

  const onDelete = () => {
    if (aid && documentMenu?._id) {
      setDisabled((pre) => !pre);
      admissionRemoveDocument({
        aid: aid,
        did: documentMenu?._id,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  const onOpenEdit = () => {
    setEdit((pre) => !pre);
  };
  const onEdit = (val) => {
    if (aid && documentMenu?._id) {
      onOpenEdit();
      setDisabled((pre) => !pre);
      admissionUpdateDocument({
        aid: aid,
        editDocument: {
          doc: documentMenu?._id,
          ...val,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };
  return edit ? (
    <AddRequiredDocument
      isEdit
      onClose={onOpenEdit}
      onEdit={onEdit}
      documentMenu={documentMenu}
    />
  ) : (
    <ModalMenu onClose={onClose} disabled={disabled}>
      <h6 onClick={onOpenEdit}>{t("edit")}</h6>
      <h6 onClick={onDelete}>{t("delete")}</h6>
    </ModalMenu>
  );
};

export default RequiredDocumentCardMenu;
